::-webkit-scrollbar {
    width: 0;
}

.main {
    width: 100%;
    height: 100%;
}

.main-light {
    background-color: #f1f1f1;
    color: rgb(20, 20, 20);
}

.main-dark {
    background-color: rgb(20, 20, 20);
    color: #f1f1f1;
}

.alert-container {
    height: 60px;
}

.textarea {
    resize: none;
}

.main-btn {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

@media screen and (max-width: 766px) {
    .main-btn {
        flex-direction: column-reverse;
        align-items: flex-end;
        justify-content: center;
        row-gap: .75rem;
    }
}

.functions-btn-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 1rem;
}

.about-container {
    height: 100vh;
}

.about-hr-line {
    width: 100%;
    height: 1px;
    background-color: black;
}

.about-description {
    font-size: 1.05rem;
}

.about {
    color: black;
    font-weight: 400;
}